
import {Component, Vue} from "vue-property-decorator";

@Component({
  components: {}
})
export default class Emptyflipbook extends Vue {
  isShowFront = true;

  flip() {
    this.isShowFront = !this.isShowFront;
  }
  beforeUpdate() {
    setTimeout(() => {
      this.isShowFront = true;
    }, 3000);
  }
}

