import { Component, Vue } from "vue-property-decorator";

import { namespace } from 'vuex-class';
const SubjectsModule = namespace('subjects');
const GoogleBooksAPI = namespace('GoogleBooksAPI');

import Slider from '@/components/Slider/index.vue';
import Loader from '@/components/Loader/index.vue';
import MenuLeft from '@/components/MenuLeft/index.vue';
import ModalBookSaved from '@/components/ModalBookSaved/index.vue';
import ScrollToTopButton from '@/components/ScrollToTopButton/index.vue';

@Component({
  components: { Slider, ScrollToTopButton, Loader, MenuLeft, ModalBookSaved }
})
export default class Subjects extends Vue {

  languages: any = ['en','de','fr','ru','uk'];
  defaultLang: string = 'en';

  @SubjectsModule.Action('MultipleRequest') MultipleRequest: any;
  @SubjectsModule.Action('actClearSubjectsArr') actClearSubjectsArr: any;
  @GoogleBooksAPI.Action('actHaveReadBooksNumber') actHaveReadBooksNumber: any;

  @SubjectsModule.Getter private getArt: any;
  @SubjectsModule.Getter private getFantasy: any;
  @SubjectsModule.Getter private getFiction: any;
  @SubjectsModule.Getter private getRomance: any;
  @SubjectsModule.Getter private getInspirational: any;
  @SubjectsModule.Getter private getBusiness: any;
  @SubjectsModule.Getter private getHistory: any;
  @SubjectsModule.Getter private getMind: any;
  @SubjectsModule.Getter private getSelf: any;
  @SubjectsModule.Getter private getSports: any;
  @GoogleBooksAPI.Getter private getSavedBookFromSubjects: any;

  SelectLang(language: string) {
    this.$router.push(`/${this.$route.name}?language=${language}`);
    this.actClearSubjectsArr();
    this.MultipleRequest(language);
  }

  beforeMount() {
    const language = this.$route.query.language;
    if (language) {
       this.MultipleRequest(language);
    } else {
      this.$router.push(`/${this.$route.name}?language=en`);
      this.MultipleRequest('en');
    }

    this.actHaveReadBooksNumber();
  }
}