
import { Vue, Component, Prop } from 'vue-property-decorator';

import { namespace } from 'vuex-class';
const GoogleBooksAPI = namespace('GoogleBooksAPI');

@Component
export default class Flipbook extends Vue {
  @Prop() book: any;

  data() {
    return {
      title: this.book.volumeInfo.title,
      imageLinks: this.book.volumeInfo.imageLinks,
      description: this.book.volumeInfo.description,
      authors: this.book.volumeInfo.authors
    }
}

  @GoogleBooksAPI.Getter private getShelvesList: any;
  @GoogleBooksAPI.Getter private getCurrentShelf: any;
  @GoogleBooksAPI.Getter private getCurrentShelfName: any;
  @GoogleBooksAPI.Getter private getCurrentShelfAccess: any;
  @GoogleBooksAPI.Action('actAddToShelf') actAddToShelf: any;
  @GoogleBooksAPI.Action('actSavedBookShown') actSavedBookShown: any;

  isShowFront = true;

  flip() {
    this.isShowFront = !this.isShowFront;
  }
  addBook() {
    this.actSavedBookShown(false);
    this.actAddToShelf({bookID: this.book.id, shelfID: this.getCurrentShelf, isSubjectsPage: true, book: this.book});
    console.log("FlipBook", this.book)
  }
  beforeUpdate() {
    setTimeout(() => {
      this.isShowFront = true;
    }, 3000);
  }

}

