export default class Slider extends Vue {
  @Prop(String) Subject: any;
  @Prop() SubjectArr: any;

  breakpoints: Object = {
      1260: {
        slideRatio: 1 / 4,
        visibleSlides: 5,
        slideMultiple: 5,
        bulletsOutside: true,
      },
      1100: {
        slideRatio: 1 / 4,
        visibleSlides: 4,
        slideMultiple: 4,
        bulletsOutside: true,
      },
      960: {
        slideRatio: 1 / 3,
        visibleSlides: 3,
        slideMultiple: 3,
        bulletsOutside: true,
      },
      800: {
        slideRatio: 1 / 3,
        arrows: false,
        visibleSlides: 3,
        slideMultiple: 3,
        bulletsOutside: true,
      },
      760: {
        slideRatio: 1 / 2,
        visibleSlides: 2,
        slideMultiple: 2,
        bulletsOutside: true,
      },
      600: {
        slideRatio: 1,
        visibleSlides: 2,
        slideMultiple: 2,
        bullets: false
      },
      480: {
        slideRatio: 1,
        visibleSlides: 1,
        slideMultiple: 1,
        bullets: false
      },
  }

}